import { Box, Typography, IconButton, useMediaQuery} from '@mui/material';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // specific CSS
import  NavigateBeforeIcon  from '@mui/icons-material/NavigateBefore';
import  NavigateNextIcon  from '@mui/icons-material/NavigateNext';
import { shades } from '../../theme';








//  imports all images from assets folder

const importAll = (r) =>
r.keys().reduce((acc, item) => {
    acc[item.replace("./", "")] = r(item);
    return acc;
}, {});

const heroTextureImports = importAll(
    require.context("../../assets", false, /\.(png|jpe?g|svg)$/)
);

const MainCarousel = () => {
    const isNonMobile = useMediaQuery("min-width:600px");
  return (
    <Carousel 
    infiniteLoop={true}
    showThumbs={false}
    showIndicators={false}
    showStatus={false}
    navigation={true}
    
    
    renderArrowPrev={(onClickHandler, hasPrev, label) => (
    <IconButton 
    onClick={onClickHandler}
    sx={{
        position: 'absolute',
        top: '50%',
        left: '0',
        color: 'black',
        padding: '5px',
        zIndex: '10'
    }}
    >
        <NavigateBeforeIcon  sx={{ fontSize: 60 }}/>

    </IconButton>
    
    )}
    renderArrowNext={(onClickHandler, hasNext, label) => (
        <IconButton onClick={onClickHandler}
        sx={{
            position: 'absolute',
            top: '50%',
            right: '0',
            color: 'black',
            padding: '5px',
            zIndex: '10',
        }}
        >
            <NavigateNextIcon  sx={{ fontSize: 60 }}/>
    
        </IconButton>
        
        )}
    >
        {Object.values(heroTextureImports).map((texture, index) =>(
            <Box key={`carousel-image-${index}`}>
                <img 
                src={texture}
                alt={`carousel-${index}`}
                style={{
                    width: '50%',
                    height: '600px',
                    objectFit: 'cover',
                    backgroundAttachment: 'fixed',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat',
                    // backgroundSize: 'contain',

                    
                }}/>
                <Box
                color='white'
                padding='20px'
                borderRadius='1px'
                textAlign='left'
                backgroundColor='rgb(0,0,0,0.4)'
                position='absolute'
                top='56%'
                left={isNonMobile ? '10%' : '0'}
                right={isNonMobile ? undefined : '0'}
                margin={isNonMobile ? undefined : '0 10%'}
                maxWidth={isNonMobile ? undefined : '280px'}
                >
                    <Typography color={shades.secondary[200]}>-- NEW ITEMS</Typography>
                    <Typography variant='h4'>Summer Sale</Typography>
                    <Typography fontWeight='bold' color={shades.secondary[300]} sx={{ textDecoration: 'underline'}}>Discover More</Typography>

                </Box>

            </Box>

        ))} 

    </Carousel>
  );
};

export default MainCarousel